@import '../../../../utils/breakpoints';


.security {
  padding: 16px 44px;
  &__title {
    margin-left: 21px;
  }
  &__options {
    margin-top: 16px;
    margin-bottom: 24px;
    background: linear-gradient(0deg, rgba(25, 59, 103, 0.05), rgba(25, 59, 103, 0.05)), #FFFFFF;
    border-radius: 4px;
    padding: 28px 21px;
  }
  .options {
    &__title {
      font-size: 22px;
      line-height: 125.3%;
      color: #192434;
      font-weight: normal;
    }
    &__item {
      margin-top: 16px;
      display: flex;
      align-items: center;
      font-size: 20px;
      line-height: 125.3%;
      color: #192434;
      input[type=radio] {
        margin-right: 8px;
        display: block;
        width: 24px;
        height: 24px;
      }
    }
  }
  &__ip {
    margin-left: 21px;
    font-size: 16px;
    line-height: 125.3%;
    margin-top: 16px;
    color: rgba(28, 48, 74, 0.5);
    &-main {
      font-size: 22px;
      color: #192434;
    }
    &-value {
      margin-top: 11px;
      color: rgba(24, 39, 58, 0.94);
    }
  }
}

@media (max-width: $mobile-width) { 
  .security {
    animation: openPageTranslate ease-in-out 0.6s forwards;
    padding: 4px;
    &__title {
      display: none;
    }
    .options {
      &__title {
        font-size: 22px;
        line-height: 125.3%;
        color: #192434;
        font-weight: normal;
      }
      &__item {
        margin-top: 16px;
        display: flex;
        align-items: center;
        font-size: 16px;
        line-height: 125.3%;
        color: #192434;
        input[type=radio] {
          margin-right: 8px;
          display: block;
          width: 16px;
          height: 16px;
        }
      }
    }
  }
}